.app {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  gap: 20px;
  height: 100vh;
  font-family: Cambria, Cochin, Georgia, Times, "Times New Roman", serif;
  background-color: #1E497D;
}

input[type="text"],
input[type="password"] {
  font-family: "Kanit";
  height: 25x;
  width: 300px;
  border: 1px solid rgba(0, 0, 0, 0.2);
}

input[type="submit"] {
  margin-top: 10px;
  cursor: pointer;
  font-size: 15px;
  font-family: "Kanit";
  background: linear-gradient(to right, #1E497D, #3481de);
  border: 0;
  border-radius: 10%;
  color: rgb(255, 255, 255);
  padding: 10px 20px;
}

input[type="submit"]:hover {
  background: #2fc15b;
}

.button-container {
  display: flex;
  justify-content: center;
}

.login-form {
  font-family: "Kanit";
  background-color: white;
  border-radius: 10%;
  padding: 2rem;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.list-container {
  display: flex;
}

.error {
  color: red;
  font-size: 12px;
}

.title {
  font-size: 25px;
  margin-bottom: 20px;
  font-family: "Kanit";
  text-align: center;
}

.input-container {
  font-family: "Kanit";
  display: flex;
  flex-direction: column;
  gap: 8px;
  margin: 10px;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
